.typography {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 45px;
  }

  &__font {
    height: 150px;
    margin-top: 110px;
    display: flex;
    justify-content: space-between;

    &:nth-child(2) {
      margin-top: 0;
    }
  }

  &__info-title {

    &-platform {
      color: #686868;
      font-size: 14px;
    }
  }

  &__font-properties {
    margin-top: 16px;
  }

  &__font-property {
    display: grid;
    grid-template-columns: 100px 150px;
    color: #626262;
    font-family: "Open Sans";
    font-size: 14px;
    margin-top: 16px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__property-title {
  }

  &__property-values {
  }

  &__property-value {
    margin-top: 16px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__example {
    display: flex;
    //align-items: center;
    margin-left: 6px;
    flex-grow: 1;
    padding-top: 23px;
    box-sizing: border-box;

    &.heading1 {
      font-family: "Overpass";
      color: #393939;
      font-size: 50px;
      line-height: 60px;
    }

    &.heading2 {
      font-family: "Overpass";
      color: #393939;
      font-size: 42px;
      line-height: 51px;
    }

    &.heading3 {
      font-family: "Overpass";
      color: #393939;
      font-size: 26px;
      line-height: 40px;
    }

    &.heading4 {
      font-family: "Overpass";
      color: #393939;
      font-size: 22px;
      line-height: 40px;
    }

    &.heading5 {
      font-family: "Source Sans Pro";
      color: #393939;
      font-size: 18px;
      line-height: 34px;
    }

    &.heading1tablet {
      font-family: "Overpass";
      color: #393939;
      font-size: 42px;
      line-height: 52px;
    }

    &.heading1mobile {
      font-family: "Overpass";
      color: #393939;
      font-size: 30px;
      line-height: 45px;
    }

    &.heading2mobile {
      font-family: "Overpass";
      color: #393939;
      font-size: 26px;
      line-height: 39px;
    }

    &.heading3mobile {
      font-family: "Overpass";
      color: #393939;
      font-size: 22px;
      line-height: 37px;
    }

    &.heading5mobile {
      color: #393939;
      font-family: "Source Sans Pro";
      font-size: 16px;
      line-height: 33px;
    }

    &.paragraph1 {
      font-family: "Source Sans Pro";
      color: #393939;
      font-size: 18px;
      line-height: 26px;
      padding-top: 27px;
      padding-right: 5px;
    }

    &.paragraph2 {
      color: #393939;
      font-family: "Source Sans Pro";
      font-size: 16px;
      line-height: 22px;
      padding-top: 27px;
    }

    &.paragraph3 {
      color: #393939;
      font-family: "Source Sans Pro";
      font-size: 14px;
      line-height: 20px;
      padding-top: 28px;
    }
  }
}