.font-families {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 45px;
  }

  &__font {
    margin-top: 129px;
    display: flex;
    justify-content: space-between;

    &:nth-child(2) {
      margin-top: 0;
    }
  }

  &__font-properties {
    margin-top: 16px;
  }

  &__font-property {
    display: grid;
    grid-template-columns: 100px 150px;
    color: #626262;
    font-family: "Open Sans";
    font-size: 14px;
    margin-top: 16px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__property-title {
  }

  &__property-values {
  }

  &__property-value {
    margin-top: 16px;
    color: #393939;

    &:first-child {
      margin-top: 0;
    }
  }

  &__example {
    display: flex;
    align-items: center;
    height: 260px;
    margin-left: 6px;
    border: 1px solid #d0d0d0;
    flex-grow: 1;
    padding-bottom: 10px;
    box-sizing: border-box;

    &.overpass {
      font-family: "Overpass";
      font-weight: 600;
    }

    &.source-sans-pro {
      font-family: "Source Sans Pro";
      font-weight: 400;
    }
  }

  &__example-literal {
    display: flex;
    //justify-content: center;
    align-items: center;
    color: #222222;
    font-size: 120px;
    width: 265px;
    padding-left: 75px;
    box-sizing: border-box;
    line-height: 300px;
  }

  &__example-word {
    color: #222222;
    font-size: 60px;
    padding-top: 10px;
    line-height: 1;
  }
}