@import url(https://fonts.googleapis.com/css?family=Overpass:600|Source+Sans+Pro:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px; }

.container {
  width: 1000px; }

hr {
  border: 0;
  border-bottom: 1px solid #cccccc; }

.hr_header_font-families {
  margin: 42px 0 93px 0; }

.hr_font-families_typography {
  margin: 102px 0 93px 0; }

.typography__title {
  color: #393939;
  font-family: "Open Sans", sans-serif;
  font-size: 34px;
  font-weight: 400;
  margin-bottom: 45px; }

.typography__font {
  height: 150px;
  margin-top: 110px;
  display: flex;
  justify-content: space-between; }
  .typography__font:nth-child(2) {
    margin-top: 0; }

.typography__info-title-platform {
  color: #686868;
  font-size: 14px; }

.typography__font-properties {
  margin-top: 16px; }

.typography__font-property {
  display: grid;
  grid-template-columns: 100px 150px;
  color: #626262;
  font-family: "Open Sans";
  font-size: 14px;
  margin-top: 16px; }
  .typography__font-property:first-child {
    margin-top: 0; }

.typography__property-value {
  margin-top: 16px; }
  .typography__property-value:first-child {
    margin-top: 0; }

.typography__example {
  display: flex;
  margin-left: 6px;
  flex-grow: 1;
  padding-top: 23px;
  box-sizing: border-box; }
  .typography__example.heading1 {
    font-family: "Overpass";
    color: #393939;
    font-size: 50px;
    line-height: 60px; }
  .typography__example.heading2 {
    font-family: "Overpass";
    color: #393939;
    font-size: 42px;
    line-height: 51px; }
  .typography__example.heading3 {
    font-family: "Overpass";
    color: #393939;
    font-size: 26px;
    line-height: 40px; }
  .typography__example.heading4 {
    font-family: "Overpass";
    color: #393939;
    font-size: 22px;
    line-height: 40px; }
  .typography__example.heading5 {
    font-family: "Source Sans Pro";
    color: #393939;
    font-size: 18px;
    line-height: 34px; }
  .typography__example.heading1tablet {
    font-family: "Overpass";
    color: #393939;
    font-size: 42px;
    line-height: 52px; }
  .typography__example.heading1mobile {
    font-family: "Overpass";
    color: #393939;
    font-size: 30px;
    line-height: 45px; }
  .typography__example.heading2mobile {
    font-family: "Overpass";
    color: #393939;
    font-size: 26px;
    line-height: 39px; }
  .typography__example.heading3mobile {
    font-family: "Overpass";
    color: #393939;
    font-size: 22px;
    line-height: 37px; }
  .typography__example.heading5mobile {
    color: #393939;
    font-family: "Source Sans Pro";
    font-size: 16px;
    line-height: 33px; }
  .typography__example.paragraph1 {
    font-family: "Source Sans Pro";
    color: #393939;
    font-size: 18px;
    line-height: 26px;
    padding-top: 27px;
    padding-right: 5px; }
  .typography__example.paragraph2 {
    color: #393939;
    font-family: "Source Sans Pro";
    font-size: 16px;
    line-height: 22px;
    padding-top: 27px; }
  .typography__example.paragraph3 {
    color: #393939;
    font-family: "Source Sans Pro";
    font-size: 14px;
    line-height: 20px;
    padding-top: 28px; }

.font-families__title {
  color: #393939;
  font-family: "Open Sans", sans-serif;
  font-size: 34px;
  font-weight: 400;
  margin-bottom: 45px; }

.font-families__font {
  margin-top: 129px;
  display: flex;
  justify-content: space-between; }
  .font-families__font:nth-child(2) {
    margin-top: 0; }

.font-families__font-properties {
  margin-top: 16px; }

.font-families__font-property {
  display: grid;
  grid-template-columns: 100px 150px;
  color: #626262;
  font-family: "Open Sans";
  font-size: 14px;
  margin-top: 16px; }
