    margin-top: 0; }

.font-families__property-value {
  margin-top: 16px;
  color: #393939; }
  .font-families__property-value:first-child {
    margin-top: 0; }

.font-families__example {
  display: flex;
  align-items: center;
  height: 260px;
  margin-left: 6px;
