  box-sizing: border-box; }
  .font-families__example.overpass {
    font-family: "Overpass";
    font-weight: 600; }
  .font-families__example.source-sans-pro {
    font-family: "Source Sans Pro";
    font-weight: 400; }

.font-families__example-literal {
  display: flex;
