  line-height: 300px; }

.font-families__example-word {
  color: #222222;
  font-size: 60px;
  padding-top: 10px;
  line-height: 1; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header__project-name {
    color: #1b242d;
    font-family: "Open Sans";
    font-size: 24px;
    text-transform: uppercase;
    padding-top: 16px;
    box-sizing: border-box; }

